          :sort-desc.sync="sortDesc"
          :sort-direction="sortDirection"
        >
          <template v-slot:cell(fecha)="row">
            {{ row.item.fecha.toString().split("-").reverse().join("/") }}
          </template>
        </b-table>
    </b-card>
	</div>
</template>

<script>
  export default {
    props: ['necesidad_internacionalizacion'],
    data() {
      return {
