          { key: 'fecha',       label: 'Fecha',   sortable: true}
        ],
        sortBy: 'fecha',
        sortDesc: true,
        sortDirection: 'desc',
      }
    }
  };
</script>
<style>
.table-necesidad-internacionalizacion-emprendimiento thead tr th{
  padding-top: .5rem;
  padding-bottom: .5rem;
}

.table-necesidad-internacionalizacion-emprendimiento thead tr th div{
  font-size: 14px;
  font-weight: 600;
  display: flex;
  justify-content: center;
}

@media screen and (min-width: 400px) {
  .table-necesidad-internacionalizacion-emprendimiento tbody tr td:nth-child(1) {
    text-align: left;
  }
  .table-necesidad-internacionalizacion-emprendimiento tbody tr td:nth-child(2) {
