              </b-col>
            </b-row>
          </b-col>

          <b-col cols="12" class="pt-2":hidden="this.descripcion==null">
            <b-row>
              <b-col class="titulo" md="3" xs="12">
                <label>Descripcin:</label>
              </b-col>
              <b-col md="9" xs="12">
                {{ this.descripcion }}
              </b-col>
            </b-row>
          </b-col>
          
          <b-col md="12" xs="12" class="pt-2" :hidden="this.palabras_claves.length==0">
