              <b-col class="titulo" md="3" xs="12">
                <label>Palabras Claves:</label>
              </b-col>
              <b-col md="9" xs="12">
                <label class="pr-1" v-for="palabra in this.palabras_claves">
                  <b-badge class="p-2" variant="primary">
                    {{ palabra.palabra_clave }}
                  </b-badge>
                </label>
              </b-col>
            </b-row>
          </b-col>

          <b-col cols="12">
            <hr width="80%">
          </b-col>

          <b-col cols="12" class="pt-2">
            <b-row sm>
              <b-col class="titulo" md="3" xs="12">
                <label>Programa:</label>
              </b-col>
              <b-col md="9" xs="12">
                {{ this.nombre_programa }}
              </b-col>
            </b-row>
          </b-col>

          <b-col cols="12" class="pt-2">
            <b-row sm>
              <b-col class="titulo" md="3" xs="12">
                <label>Gabinete / Incubadora:</label>
              </b-col>
              <b-col md="9" xs="12">
                {{ this.incubado_en }}
              </b-col>
            </b-row>
          </b-col>

          <b-col cols="12" class="pt-2">
            <b-row sm>
              <b-col class="titulo" md="3" xs="12">
                <label>Situacin Actual:</label>
              </b-col>
              <b-col md="9" xs="12">
                {{ this.situacion_actual }}
              </b-col>
            </b-row>
          </b-col>

          <b-col cols="12">
            <hr width="80%">
          </b-col>

          <b-col md="6" xs="12" class="pt-2">
            <b-row>
              <b-col class="titulo" md="6" xs="12">
                <label>Perfl:</label>
              </b-col>
              <b-col md="6" xs="12">
                {{ this.perfil }}
              </b-col>
