              <b-col class="titulo" md="6" xs="12">
                <label>Ao de inicio:</label>
              </b-col>
              <b-col md="6" xs="12">
                {{ this.anio_inicio }}
              </b-col>
            </b-row>
          </b-col>

          <b-col md="6" xs="12" class="pt-2">
            <b-row>
              <b-col class="titulo" md="6" xs="12">
                <label>Rubro primario:</label>
              </b-col>
              <b-col md="6" xs="12">
                {{ this.rubro_principal }}
              </b-col>
            </b-row>
          </b-col>

          <b-col md="6" xs="12" class="pt-2" :hidden="this.razon_social==null">
            <b-row>
              <b-col class="titulo" md="6" xs="12">
                <label>Razn Social:</label>
              </b-col>
              <b-col md="6" xs="12">
                {{ this.razon_social }}
              </b-col>
            </b-row>
          </b-col>

          <b-col md="6" xs="12" class="pt-2" :hidden="this.rubro_secundario==null">
            <b-row>
              <b-col class="titulo" md="6" xs="12">
                <label>Rubro Secundario:</label>
              </b-col>
              <b-col md="6" xs="12">
                {{ this.rubro_secundario }}
              </b-col>
            </b-row>
          </b-col>

          <b-col cols="12" :hidden="this.telefono_emprendimiento==null &&  this.correo_emprendimiento==null">
