          <b-col cols="12" class="pt-2" :hidden="this.telefono_emprendimiento==null">
            <b-row>
              <b-col class="titulo" md="3" xs="12">
                <label>Telfono:</label>
              </b-col>
              <b-col md="9" xs="12">
                {{ this.telefono_emprendimiento }}
              </b-col>
            </b-row>
          </b-col>     
        </b-row>
      </b-container>
