  export default {
    props: ['nombre_programa','incubado_en', 'situacion_actual', 'nombre','descripcion','perfil','razon_social','rubro_principal','anio_inicio','rubro_secundario','palabras_claves','correo_emprendimiento','telefono_emprendimiento']
  };
</script>
<style>
@media (min-width: 768.98px) { 
  .titulo{
    text-align: right;
  }
}
</style>