<template>
  <div class="content">
    <div class="container-fluid">
      <div class="row">
        <div class="col-md-12">
          
          <InfoEmprendimiento />
          <EstadioPlataforma />
          <MDNEmprendimiento />
          <PDNEmprendimiento />
          <PitchEmprendimiento />
          
          <SolidezEquipoEmprendimiento />

          <ProteccionTecnologica />
          <FormalizacionJuridica />
          <FinanciamientoObtenidoEmprendimiento />
          <ConcursosYEventosEmprendimiento />
          <CapacitacionesEmprendimiento />
          <AsesoriasEmprendimiento />
          <MentoriasEmprendimiento />
          <ObservacionesEmprendimiento />
          <MercadoInternacionalEmprendimiento />
          <CooperacionInternacionalEmprendimiento />
          <RedesInternacionalesEmprendimiento />
          
          <div class="col-md-12 pt-4 pb-4">
            <div class="row">
              <div class="col-sm-12">

                <router-link class="mt-3 btn btn-danger btn-fill"
                  :to="({name: 'createBIO3'})" >Volver
                </router-link>

                <b-button 
                  class="mt-3 btn btn-info btn-fill" 
                  style="float: right;"
                  @click="siguiente">
                  Siguiente
                </b-button>
              </div>
            </div>
          </div>
          
          <b-modal size="md" id="errors" hide-footer>
            <div class="row">
              <div class="col-md-12 pt-3">
                <ul>
                  <li v-for="item in errors">
                    {{ item.error }}
                  </li>
                </ul>
              </div>
            </div>
            <div class="pt-4">
              <b-button 
                class="mt-3 pull-right btn-danger btn-fill" 
                @click="resetModalErrors">
                Aceptar
              </b-button>
            </div>
          </b-modal>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from "axios";
  const URL_API = process.env.VUE_APP_URL_API;

  import InfoEmprendimiento from 'src/components/CreateEmprendimiento/InfoEmprendimiento.vue'
  import EstadioPlataforma from 'src/components/CreateEmprendimiento/EstadioPlataforma.vue'
  import MDNEmprendimiento from 'src/components/CreateEmprendimiento/MDNEmprendimiento.vue'
  import PDNEmprendimiento from 'src/components/CreateEmprendimiento/PDNEmprendimiento.vue'
  import PitchEmprendimiento from 'src/components/CreateEmprendimiento/PitchEmprendimiento.vue'

  import SolidezEquipoEmprendimiento from 'src/components/CreateEmprendimiento/SolidezEquipoEmprendimiento.vue'

  import ProteccionTecnologica from 'src/components/CreateEmprendimiento/ProteccionTecnologica.vue'
  import FormalizacionJuridica from 'src/components/CreateEmprendimiento/FormalizacionJuridica.vue'

  import FinanciamientoObtenidoEmprendimiento from 'src/components/CreateEmprendimiento/FinanciamientoObtenidoEmprendimiento.vue'
  import ConcursosYEventosEmprendimiento from 'src/components/CreateEmprendimiento/ConcursosYEventosEmprendimiento.vue'
  import CapacitacionesEmprendimiento from 'src/components/CreateEmprendimiento/CapacitacionesEmprendimiento.vue'
  import AsesoriasEmprendimiento from 'src/components/CreateEmprendimiento/AsesoriasEmprendimiento.vue'
  import MentoriasEmprendimiento from 'src/components/CreateEmprendimiento/MentoriasEmprendimiento.vue'
  import ObservacionesEmprendimiento from 'src/components/CreateEmprendimiento/ObservacionesEmprendimiento.vue'
  import MercadoInternacionalEmprendimiento from 'src/components/CreateEmprendimiento/MercadoInternacionalEmprendimiento.vue'
  import CooperacionInternacionalEmprendimiento from 'src/components/CreateEmprendimiento/CooperacionInternacionalEmprendimiento.vue'
  import RedesInternacionalesEmprendimiento from 'src/components/CreateEmprendimiento/RedesInternacionalesEmprendimiento.vue'

  export default {
    components: {
      InfoEmprendimiento,
      EstadioPlataforma, 
      MDNEmprendimiento, 
      PDNEmprendimiento, 
      PitchEmprendimiento, 

      SolidezEquipoEmprendimiento,

      ProteccionTecnologica, 
      FormalizacionJuridica,
      FinanciamientoObtenidoEmprendimiento,
      ConcursosYEventosEmprendimiento,
      CapacitacionesEmprendimiento,
      AsesoriasEmprendimiento,
      MentoriasEmprendimiento,
      ObservacionesEmprendimiento,
      MercadoInternacionalEmprendimiento,
      CooperacionInternacionalEmprendimiento,
      RedesInternacionalesEmprendimiento
    },

    data () {
      return { 
        errors:[],
      }
    }, 

    methods:{
      currentRoute(){
          this.$store.commit('setSubTitulo', {setSubTitulo: 'nuevo 4/5'})
      },

      verificarEmprendimiento(){
        if(this.$store.state.emprendimiento_create==false){
          this.$router.push( {name:'createBIO1'});
        }
      },

      siguiente(){
        this.errors=[];

        if(this.$store.state.emprendimiento.formalizacion_juridica=='si'){
          if(this.$store.state.emprendimiento.fecha_estructura_juridica==''){
            this.errors.push({'error':'Seleccione la FECHA DE FORMALIZACIN JURIDICA del emprendimiento antes de continuar'});
          }  
        }

        if(this.errors[0]){
          this.$root.$emit('bv::show::modal','errors');
        }else{
          this.$router.push( {name:'createBIO5'});
        }
      },

      resetModalErrors(){
        this.errors=[];
        this.$root.$emit('bv::hide::modal','errors');
      },
    },

    created(){
      this.$Progress.start();
      window.scrollTo(0,-1);
      this.verificarEmprendimiento();
      this.currentRoute();
      this.$Progress.finish();
    }
  };
</script>