<template>
  <div class="content pt-4"> 
    <b-container fluid>
      <div class="card">
        <div class="col-md-12">
          <!-- User Interface controls -->
          <b-row>
            <b-col lg="6" class="my-4">
              <b-button 
                @click="nuevaCapacitacion" 
                class="btn btn-info btn-fill">
                <span class="fa fa-plus"></span> Nuevo
              </b-button>
            </b-col>
            <b-col lg="6" class="my-4">
              <b-form-group
                label-cols-sm="7"
                label-align-sm="right"
                label-size="md"
                label-for="filterInput"  
              >
              <b-form-input
                v-model="filter"
                type="search"
                id="filterInput"
                autocomplete="off"
                size="md"
                placeholder="Buscr"
              ></b-form-input>
              </b-form-group>
            </b-col>
          </b-row>
        </div>
        <!-- Main table element -->
        <div class="col-md-12 pt-4">
          <b-table
            class="table-capacitaciones"
            hover
            head-variant="dark"
            stacked="md"
            :items="capacitaciones_institucionales"
            :fields="fields"
            :current-page="currentPage"
            :per-page="perPage"
            :filter="filter"
            :filterIncludedFields="filterOn"
            :sort-by.sync="sortBy"
            :sort-desc.sync="sortDesc"
            :sort-direction="sortDirection"
            @filtered="onFiltered"
          >
            <template v-slot:cell(actions)="row">
              <b-button size="sm" @click="editCapacitacion(row.item, row.index, $event.target)" class="btn btn-warning btn-fill mr-1">
                <span class="fa fa-pencil"></span>
              </b-button>
              <b-button class="btn-danger btn-fill" size="sm" @click="deleteCapacitacion(row.item, row.index, $event.target)">
                <span class="fa fa-trash"></span>
              </b-button>
            </template>

            <template v-slot:row-details="row">
              <b-card>
                <ul>
                  <li v-for="(value, key) in row.item" :key="key">{{ key }}: {{ value }}</li>
                </ul>
              </b-card>
            </template>
          </b-table>
        </div>
        <b-row>
          <b-col sm="5" md="3" class="my-1">
            <b-form-group
              label="Por pagina"
              label-cols-sm="8"
              label-cols-md="8"
              label-cols-lg="8"
              label-align-sm="right"
              label-size="sm"
              label-for="perPageSelect"
              class="mb-0"
            >
              <b-form-select
                v-model="perPage"
                id="perPageSelect"
                size="sm"
                :options="pageOptions"
              ></b-form-select>
            </b-form-group>
          </b-col>
          <b-col sm="7" md="3" class="my-1">
            <b-pagination
              v-model="currentPage"
              :total-rows="totalRows"
              :per-page="perPage"
              align="fill"
              size="sm"
              class="my-0"
            ></b-pagination>
          </b-col>
        </b-row>
      </div>
      <b-modal 
        size="lg" 
        id="create-modal" 
        hide-footer
        title="Nuevo">
        <b-row>
          <b-col sm="12">
            <label>Capacitacin:</label>
            <b-form-input 
              v-model="capacitacion" 
              @keydown.enter="addCapacitacion" 
              required 
              autocomplete="off">
            </b-form-input>
          </b-col>

          <b-col sm="12" class="my-3">
            <label>Institucin:</label>
            <b-form-select
              v-model="institucion" 
              :options="instituciones">
            </b-form-select>
          </b-col>

          <b-col sm="12" class="my-3" :hidden="institucion!='Otra'">
            <label>Otra institucin:</label>
            <b-form-input 
              type="text" 
              autocomplete="off" 
              v-model="otra_institucion"
              @keydown.enter="addCapacitacion">
            </b-form-input>
          </b-col>
        </b-row>
        <b-row>
          <b-col class="pt-4">
            <b-button 
              class="mt-3 pull-left btn-danger btn-fill" 
              @click="resetModal">Cancelar  
            </b-button>
            <b-button 
              class="mt-3 pull-right btn-info btn-fill"
              @click="addCapacitacion"
              :disabled="!capacitacion">Agregar
            </b-button>
          </b-col>
        </b-row>
      </b-modal>

      <!-- Edit modal -->
      <b-modal 
        id="edit-modal" 
        size="lg" 
        hide-footer
        title="Editar">
        <b-row>
          <b-col sm="12">
            <label>Capacitacin:</label>
            <b-form-input 
              v-model="capacitacion" 
              @keydown.enter="updateCapacitacion" 
              required 
              autocomplete="off">
            </b-form-input>
          </b-col>

          <b-col sm="12" class="my-1
            ">
            <label>Institucin:</label>
            <b-form-select
              v-model="institucion" 
              :options="instituciones">
            </b-form-select>
          </b-col>

          <b-col sm="12" class="my-1" :hidden="institucion!='Otra'">
            <label>Otra institucin:</label>
            <b-form-input 
              type="text" 
              autocomplete="off" 
              v-model="otra_institucion"
              @keydown.enter="updateCapacitacion">
            </b-form-input>
          </b-col>
        </b-row>
        <div class="pt-4">
          <b-button 
            class="mt-3 pull-left btn-danger btn-fill"
            @click="resetModal">Cancelar
          </b-button>
          <b-button 
            class="mt-3 pull-right btn-info btn-fill"
            @click="updateCapacitacion"
            :disabled="!capacitacion">Actualizar
          </b-button>
        </div>
      </b-modal>

    </b-container>
  </div>
</template>

<script>
  import axios from "axios";
  const URL_API = process.env.VUE_APP_URL_API;

  export default {
    data() {
      return {
        capacitaciones_institucionales: [],
        capacitacion: '',

        editindex: null,
        deleteIndex: null,

        institucion: 'Universidad Nacional del Litoral',
        instituciones:[
          {'value': 'Universidad Nacional del Litoral', 'text':'Universidad Nacional del Litoral'},
          {'value': 'Otra', 'text':'Otra'}
        ],
        
        otra_institucion: null,

        fields: [
          { key: 'id', label: '#', sortable: true, thStyle: {width:'7%'}},
          { key: 'capacitacion', label: 'Capacitacin', sortable: true, sortDirection: 'desc', thStyle: {width:'45%'}},
          { key: 'institucion', label: 'Institucin', sortable: true, sortDirection: 'desc', thStyle: {width:'35%'}},
          { key: 'actions', label: 'Acciones'}
        ],
        totalRows: 1,
        pageOptions: [10, 25, 50, 100],
        sortBy: '',
        sortDesc: false,
        sortDirection: 'asc',
        filter: null,
        filterOn: [],
        editModal: {
          id: 'info-modal',
          title: '',
          content: ''
        }
      }
    },

    computed: {
      sortOptions() {
        // Create an options list from our fields
        return this.fields
          .filter(f => f.sortable)
          .map(f => {
            return { text: f.label, value: f.key }
          })
      },
      
      perPage:{
        get(){
          return this.$store.state.perPage
        },
        set(value){
          this.$store.commit('setPerPage', {perPage: value})
        }
      },

      currentPage:{
        get(){
          return this.$store.state.currentPage
        },
        set(value){
          this.$store.commit('setCurrentPage', {currentPage: value})
        }
      }
    },

    mounted() {
      // Set the initial number of items
      this.$store.commit('setTitulo', {setTitulo: 'Configuracin'});
      this.$store.commit('setSubTitulo', {setSubTitulo: 'Capacitaciones Institucionales'});
    },

    methods: {
      onFiltered(filteredItems) {
        // Trigger pagination to update the number of buttons/pages due to filtering
        this.totalRows = filteredItems.length
        this.currentPage = 1
      },

      async getCapacitaciones(){
        await axios.get(URL_API+'api/capacitaciones-institucionales').then(response => {
          this.capacitaciones_institucionales = response.data;
          this.totalRows = this.capacitaciones_institucionales.length;
          this.$Progress.finish()
        }).catch(e=>{
          alert("Oops ha ocurrido un error durante la carga, intente mas tarde.");
          console.log(e);
          this.$router.push({ name: 'EmprendimientosUNL' })
        })
      },

      nuevaCapacitacion() {
        this.resetModal();
        this.$root.$emit('bv::show::modal', 'create-modal');
      },

      async addCapacitacion(){
        var institucion='';
        if(this.institucion!='Otra'){
          institucion= this.institucion
        }else{
          institucion= this.otra_institucion
        }

        axios.post(URL_API+'api/capacitaciones-institucionales', {
            capacitacion: this.capacitacion,
            institucion: institucion
        }).then(response => {
          this.resetModal();
          this.getCapacitaciones();
        }).catch(e => {
            console.log(e);
        });
        this.$root.$emit('bv::hide::modal','create-modal')
      },

      resetModal(item, index, button) {
        this.capacitacion = '';
        this.institucion = 'Universidad Nacional del Litoral';
        this.otra_institucion = '';
        this.$root.$emit('bv::hide::modal','create-modal');
        this.$root.$emit('bv::hide::modal', 'edit-modal')
      },

      editCapacitacion(item, index, button) {
        this.editindex = item.id;
        this.capacitacion = item.capacitacion;
        if(item.institucion=='Universidad Nacional del Litoral'){
          this.institucion = item.institucion;
        }else{
          this.institucion = 'Otra';
          this.otra_institucion = item.institucion;
        }
        this.$root.$emit('bv::show::modal','edit-modal');
      },

      async updateCapacitacion(item, index, button){
        var institucion='';
        if(this.institucion!='Otra'){
          institucion= this.institucion
        }else{
          institucion= this.otra_institucion
        }

        await axios.put(URL_API+'api/capacitaciones-institucionales/'+this.editindex, {
            capacitacion: this.capacitacion,
            institucion: institucion
        }).then(response => {
          this.getCapacitaciones();
          this.resetModal();
        }).catch(e => {
            console.log(e);
            this.resetModal();
        });
      },

      async deleteCapacitacion(item, index, button){
        this.deleteIndex = item.id;
        if (confirm('Seguro que desea eliminarlo?')) {
          axios.delete(URL_API+'api/capacitaciones-institucionales/'+this.deleteIndex)
          .then(response => {
            this.deleteIndex = null;
            this.getCapacitaciones();
          }).catch(e => {
              console.log(e);
          });
        }
      }
    },

    created(){
      this.$Progress.start(),
      this.getCapacitaciones()
    }
  }
</script>

<style>
.btn-close {
  background-color: red;
}

.table-capacitaciones {
  table-layout: fixed;
}

.table-capacitaciones thead th div{
  font-size: 14px;
  font-weight: 600;
  display: flex;
  justify-content: center;

}

.table-capacitaciones thead tr th{
  padding-top: .5rem;
  padding-bottom: .5rem;
}


@media screen and (min-width: 400px) {
  .table-capacitaciones tbody tr td:nth-child(1) {
    text-align: left;
  }
  .table-capacitaciones tbody tr td:nth-child(3) {
    text-align: left;
  }
}

@media screen and (min-width: 800px) {
  .table-capacitaciones tbody tr td:nth-child(1) {
    text-align: center;
  }

  .table-capacitaciones thead tr th:nth-child(4) {
    justify-content: center;
  }

  .table-capacitaciones tbody tr td:nth-child(4) {
    justify-content: center;
  }
}
</style>