<template>
  <div>
    <h4>Emprendimiento</h4>
    <div class="card">
      <div class="col-md-12 pt-4 pb-4">
        <div class="row">

          <div class="col-sm-6 pt-3">
            <label>Incubado en:</label>
            <b-form-select
              v-model="incubadoEmprendimiento" 
              :options="gabinetes_emprendedores" 
              autocomplete="off">
            </b-form-select>
          </div>

          <div class="col-sm-6 pt-3">
            <label>Situacin actual:</label>
            <b-form-select
              v-model="situacionActualEmprendimiento" 
              :options="situacion_actuales" 
              autocomplete="off">
            </b-form-select>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from "axios";
  const URL_API = process.env.VUE_APP_URL_API;

  export default {
    name: 'InfoEmprendimiento',
    data () {
      return {
        first_gabinete: null,
        id_gabinete: null,
        gabinetes_emprendedores: [],

        first_situacion_actual: null,
        id_situacion_actual: null,
        situacion_actuales: [],
      }
    },
    computed: {
      incubadoEmprendimiento:{
        get(){
          return this.$store.state.emprendimiento.id_incubado_en
        },
        set(value){
          this.$store.commit('setIncubadoEnEmprendimiento', {incubadoEmprendimiento: value})
        }
      },

      situacionActualEmprendimiento:{
        get(){
          return this.$store.state.emprendimiento.id_situacion_actual
        },
        set(value){
          this.$store.commit('setSituacionActualEmprendimiento', {situacionActualEmprendimiento: value})
        }
      },
    },
    methods:{
      async getGabinetesIndubadorasUNL(){
        let r = await axios.get(URL_API+'api/gabinetes-incubadoras-unl');

        var arr = [];
        var first = true;
        var aux;

        r.data.forEach(function(element, index){
          if(first==true){
            aux = element.id;
            first = false;
          }
          arr.push({'value': element.id, 'text': element.nombre });
        });

        this.first_gabinete = aux;
        this.id_gabinete = aux;
        this.gabinetes_emprendedores = arr;

        if(this.incubadoEmprendimiento==null){
          this.incubadoEmprendimiento = aux;
        }
      },

      async getSituacionActuales(){
        let r = await axios.get(URL_API+'api/situacion-actual');

        var arr = [];
        var first = true;
        var aux;

        r.data.forEach(function(element, index){
          if(first==true){
            aux = element.id;
            first = false;
          }
          arr.push({'value': element.id, 'text': element.situacion });
        });

        this.first_situacion_actual = aux;
        this.id_situacion_actual = aux;
        this.situacion_actuales = arr;

        if(this.situacionActualEmprendimiento===null){
          this.situacionActualEmprendimiento = aux;
        }
      }
    },
    created(){
      this.getSituacionActuales();
      this.getGabinetesIndubadorasUNL();
    }
  };
</script>
