              <b-row>
                <b-col class="titulo" md="3" xs="12">
                  <label>Fecha de alta:</label>
                </b-col>
                <b-col md="9" xs="12">
                  {{ this.fecha_estructura_juridica.split('-').reverse().join('/') }}
