                  <label>Tipo:</label>
                </b-col>
                <b-col md="9" xs="12">
                  {{ this.estructura_juridica }}
                </b-col>
              </b-row>
            </b-col>
          </template>

        </b-row>
      </b-container>
    </b-card>
	</div>
</template>

<script>
  export default {
    props: ['formalizacion_juridica', 'fecha_estructura_juridica', 'estructura_juridica'],
  };
</script>