<template>
	<div>
		<h4 class="pt-3">Mentorias</h4>   
    <b-card>
      <b-table
          class="table-mentorias-emprendimiento"
          hover
          stacked="md"
          head-variant="dark"
          :items="this.mentorias_emprendimiento"
          :fields="fields"
          :sort-by.sync="sortBy"
          :sort-desc.sync="sortDesc"
          :sort-direction="sortDirection"
        >
          <template v-slot:cell(actions)="row">
            <b-button 
              size="sm" 
              @click="viewMentoria(row.item, row.index, $event.target)" 
              class="btn btn-info btn-fill mr-1">
                <span class="fa fa-eye"></span>
            </b-button>
          </template>
        </b-table>
    </b-card>

    <b-modal id="viewModalMentoria"  size="lg" title="Mentora" hide-footer>
      <b-col cols="12" class="pt-1">
        <b-row>
          <b-col class="titulo" md="3" xs="12">
            <b>Mentora:</b>
          </b-col>
          <b-col md="9" xs="12">
            {{ this.mentoria_emprendimiento.mentoria }}
          </b-col>
        </b-row>
        
        <b-row>
          <b-col class="titulo" md="3" xs="12">
            <b>Mentor:</b>
          </b-col>
          <b-col md="9" xs="12">
            {{ this.mentoria_emprendimiento.mentor }}
          </b-col>
        </b-row>

        <b-row>
          <b-col class="titulo" md="3" xs="12">
            <b>Ao:</b>
          </b-col>
          <b-col md="9" xs="12">
            {{ this.mentoria_emprendimiento.anio }}
          </b-col>
        </b-row>

        <template v-if="this.mentoria_emprendimiento.memo_encuentro !== ''">
          <b-row>
            <b-col class="titulo" md="3" xs="12">
              <b>Memo encuentro:</b>
            </b-col>
            <b-col md="9" xs="12">
              {{ this.mentoria_emprendimiento.memo_encuentro }}
            </b-col>
          </b-row>
        </template>

        <b-row class="pt-4">
          <b-button class="mt-3 pull-left btn-danger btn-fill" @click="closeModal">Cerrar</b-button>
        </b-row>
      </b-col>
    </b-modal>
	</div>
</template>

<script>
  export default {
    props: ['mentorias_emprendimiento'],
    data() {
      return {
        mentoria_emprendimiento:{
          mentoria:null,
          mentor:null,
          anio: null,
          memo_encuentro: null
        },
        viewindex: null,
        fields: [
          { key: 'mentoria',  label: 'Mentora',    sortable: true, thStyle: {width:'50%'} },
          { key: 'mentor',    label: 'Mentor' ,     sortable: true, thStyle: {width:'30%'} },
          { key: 'anio',      label: 'Ao',         sortable: true},
          { key: 'actions',   label: 'Vr' }
        ],
        sortBy: 'anio',
        sortDesc: true,
        sortDirection: 'desc',
      }
    },
    methods: {
      viewMentoria(item, index, button) {

        
        this.mentoria_emprendimiento.mentoria = item.mentoria;
        this.mentoria_emprendimiento.mentor = item.mentor;
        this.mentoria_emprendimiento.anio = item.anio;
        this.mentoria_emprendimiento.memo_encuentro = item.memo_encuentro;

        this.$root.$emit('bv::show::modal', 'viewModalMentoria', button);
      },

      closeModal(item, index, button){
        this.$root.$emit('bv::hide::modal', 'viewModalMentoria', button)
      }
    }
  };
</script>
<style>
.table-mentorias-emprendimiento thead tr th{
  padding-top: .5rem;
  padding-bottom: .5rem;
}

.table-mentorias-emprendimiento thead tr th div{
  font-size: 14px;
  font-weight: 600;
  display: flex;
  justify-content: center;
}

@media screen and (min-width: 400px) {
  .table-mentorias-emprendimiento tbody tr td:nth-child(1) {
    text-align: left;
  }
  .table-mentorias-emprendimiento tbody tr td:nth-child(2) {
    text-align: left;
  }
}

@media screen and (min-width: 800px) {

  .table-mentorias-emprendimiento thead tr th:nth-child(3) {
    justify-content: center;
  }

  .table-mentorias-emprendimiento tbody tr td:nth-child(3) {
    text-align: center;
  }

  .table-mentorias-emprendimiento thead tr th:nth-child(4) {
    justify-content: center;
  }

  .table-mentorias-emprendimiento tbody tr td:nth-child(4) {
    justify-content: center;
  }

}
</style>