<template>
  <div class="content">
    <div class="container-fluid pt-4">
      <div class="row">
        <div class="col-xl-3 col-md-6">
          <stats-card>
            <div slot="header" class="icon-warning">
              <i class="fas fa-university text-info"></i>
            </div>
            <div slot="content">
              <p class="card-category">Emprendimientos <br> UNL</p>
              <h4 class="card-title">{{ emprendimientos_unl }}</h4>
            </div>
            <div slot="footer">
              <i class="fa fa-plus"></i>Emprendimientos vinculados
            </div>
          </stats-card>
        </div>
        
        <div class="col-xl-3 col-md-6">
          <stats-card>
            <div slot="header" class="icon-warning">
              <i class="fas fa-rocket text-info"></i>
            </div>
            <div slot="content">
              <p class="card-category">Programa <br> Emprendedores</p>
              <h4 class="card-title">{{ emprendimientos_PE }}</h4>
            </div>
            <div slot="footer">
              <i class="fa fa-plus"></i>Emprendimientos vinculados
            </div>
          </stats-card>
        </div>

        <div class="col-xl-3 col-md-6">
          <stats-card>
            <div slot="header" class="icon-success">
              <i class="fas fa-leaf text-info"></i>
            </div>
            <div slot="content">
              <p class="card-category">Programa <br> UNLBio</p>
              <h4 class="card-title">{{ emprendimientos_PB }}</h4>
            </div>
            <div slot="footer">
              <i class="fa fa-plus"></i>Emprendimientos vinculados
            </div>
          </stats-card>
        </div>

        <div class="col-xl-3 col-md-6">
          <stats-card>
            <div slot="header" class="icon-info">
              <i class="far fa-lightbulb text-info"></i>
            </div>
            <div slot="content">
              <p class="card-category">Programa <br> GIDE </p>
              <h4 class="card-title">{{ emprendimientos_PG }}</h4>
            </div>
            <div slot="footer">
              <i class="fa fa-plus"></i>Emprendimientos vinculados
            </div>
          </stats-card>
        </div>

      </div>
      <div class="row">
        <div class="col-md-8">
          <chart-card :chart-data="lineChart.data"
                      :chart-options="lineChart.options"
                      :responsive-options="lineChart.responsiveOptions">
            <template slot="header">
              <h4 class="card-title">Users Behavior</h4>
              <p class="card-category">24 Hours performance</p>
            </template>
            <template slot="footer">
              <hr>
              <div class="stats">
                <div class="legend">
                  <i class="fa fa-circle text-info"></i> Open
                  <i class="fa fa-circle text-danger"></i> Click
                  <i class="fa fa-circle text-warning"></i> Click Second Time
                </div>
              </div>
            </template>
          </chart-card>
        </div>

        <div class="col-md-4">
          <chart-card :chart-data="emprendedores_pieChart.data" v-if="emprendedores_pieChart.data.show" chart-type="Pie">
            <template slot="header">
              <h4 class="card-title">Emprendedores</h4>
              <p class="card-category">segn su gnero</p>
            </template>
            <template slot="footer">
              <hr>
              <div class="stats">
                <div class="legend">
                  <i class="fa fa-circle text-info"></i> Masculino
                  <i class="fa fa-circle text-danger"></i> Femenino
                  <i class="fa fa-circle text-warning"></i> Otros
                </div>
              </div>
            </template>
          </chart-card>
        </div>
      </div>


      <div class="row">
        <div class="col-md-12">
          
        </div>
      </div>

      <div class="row">
        <div class="col-md-12">
          <chart-card
            :chart-data="perfiles_emprendimientos.data"
            :chart-options="perfiles_emprendimientos.options"
            :chart-responsive-options="perfiles_emprendimientos.responsiveOptions"
            chart-type="Bar">
            <template slot="header">
              <h4 class="card-title">Perfiles de los emprendimientos</h4>
              <p class="card-category">All products including Taxes</p>
            </template>
            <template slot="footer">
              <div class="legend">
                <i class="fa fa-circle text-info"></i> Tesla Model S
                <i class="fa fa-circle text-danger"></i> BMW 5 Series
              </div>
              <hr>
              <div class="stats">
                <i class="fa fa-check"></i> Data information certified
              </div>
            </template>
          </chart-card>
        </div>

      </div>
    </div>
  </div>
</template>
<script>
  import ChartCard from 'src/components/Cards/ChartCard.vue'
  import StatsCard from 'src/components/Cards/StatsCard.vue'
  import LTable from 'src/components/Table.vue'

  import axios from "axios";
  const URL_API = process.env.VUE_APP_URL_API;

  export default {
    components: {
      LTable,
      ChartCard,
      StatsCard
    },
    data () {
      return {
        emprendimientos_unl:0,
        emprendimientos_PE: 0,
        emprendimientos_PG: 0,
        emprendimientos_PB: 0,

        emprendedores:100,
        emprendedores_masculinos:100,
        emprendedores_femeninas:0,
        emprendedores_otros:0,

        editTooltip: 'Edit Task',
        deleteTooltip: 'Remove',

        emprendedores_pieChart: {
          data: {
            labels: [100,0,0],
            series: [100,0,0],
            show: false
          }
        },
        lineChart: {
          data: {
            labels: ['9:00AM', '12:00AM', '3:00PM', '6:00PM', '9:00PM', '12:00PM', '3:00AM', '6:00AM'],
            series: [
              [287, 385, 490, 492, 554, 586, 698, 695],
              [67, 152, 143, 240, 287, 335, 435, 437],
              [23, 113, 67, 108, 190, 239, 307, 308]
            ]
          },
          options: {
            low: 0,
            high: 800,
            showArea: false,
            height: '245px',
            axisX: {
              showGrid: false
            },
            lineSmooth: true,
            showLine: true,
            showPoint: true,
            fullWidth: true,
            chartPadding: {
              right: 50
            }
          },
          responsiveOptions: [
            ['screen and (max-width: 640px)', {
              axisX: {
                labelInterpolationFnc (value) {
                  return value[0]
                }
              }
            }]
          ]
        },

        perfiles_emprendimientos: {
          data: {
            labels: ['Jan', 'Feb', 'Mar', 'Apr', 'Mai', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
            series: [
              [542, 443, 320, 780, 553, 453, 326, 434, 568, 610, 756, 895]
            ]
          },
          options: {
            axisX: {
              showGrid: true
            },
            height: '300px'
          }
        },
        tableData: {
          data: [
            {title: 'Sign contract for "What are conference organizers afraid of?"', checked: false},
            {title: 'Lines From Great Russian Literature? Or E-mails From My Boss?', checked: true},
            {
              title: 'Flooded: One year later, assessing what was lost and what was found when a ravaging rain swept through metro Detroit',
              checked: true
            },
            {title: 'Create 4 Invisible User Experiences you Never Knew About', checked: false},
            {title: 'Read "Following makes Medium better"', checked: false},
            {title: 'Unfollow 5 enemies from twitter', checked: false}
          ]
        }
      }
    },
    methods:{
      currentRoute(){
        this.$store.commit('setTitulo', {setTitulo: 'Indicadores UNL'});
        this.$store.commit('setSubTitulo', {setSubTitulo: ''});
      },

      async getIndicadores(){
        try {
          const response = await axios.get(URL_API+'api/indicadoresUNL');
          this.emprendimientos_unl = response.data.emprendimientos;
          this.emprendimientos_PE = response.data.emprendimientos_PE;
          this.emprendimientos_PG = response.data.emprendimientos_PG;
          this.emprendimientos_PB = response.data.emprendimientos_PB;

          this.emprendedores = response.data.emprendedores;
          this.emprendedores_masculinos = response.data.emprendedores_masculinos;
          this.emprendedores_femeninas = response.data.emprendedores_femeninas;
          this.emprendedores_otros = response.data.emprendedores_otros;

          this.emprendedoresPieChart();


        } catch (error) {
            console.log(error.response)
        }
      },

      emprendedoresPieChart(){
        
        let emprendedores_masculinos_porcent = this.emprendedores_masculinos*100/this.emprendedores;
        let emprendedores_femeninas_porcent = this.emprendedores_femeninas*100/this.emprendedores;
        let emprendedores_otros_porcent = this.emprendedores_otros*100/this.emprendedores;


        this.emprendedores_pieChart.data.labels[0]=emprendedores_masculinos_porcent.toFixed(2)+' %';
        this.emprendedores_pieChart.data.series[0]=emprendedores_masculinos_porcent;

        this.emprendedores_pieChart.data.labels[1]=emprendedores_femeninas_porcent.toFixed(2)+' %';
        this.emprendedores_pieChart.data.series[1]=emprendedores_femeninas_porcent;

        this.emprendedores_pieChart.data.labels[2]=emprendedores_otros_porcent.toFixed(2)+' %';
        this.emprendedores_pieChart.data.series[2]=emprendedores_otros_porcent;


        this.emprendedores_pieChart.data.show = true;
        console.log(this.emprendedores_pieChart.data);
      },

      async perfilesEmprendimientos(){
        
        try {
          const response = await axios.get(URL_API+'api/perfilesEmprendimientosUNL');
          let perfiles = response.data.perfines_emprendimiento;
          let that = this;

          perfiles.forEach(element => {
            
            let key = Object.keys(element)[0];


              that.perfiles_emprendimientos.data.label.push(key);
              that.perfiles_emprendimientos.data.series.push( element[key] );


            
   
          });


        } catch (error) {
            console.log(error.response)
        }
      }
    },

    created(){
      this.$Progress.start();
      this.currentRoute();
      this.getIndicadores();
      this.perfilesEmprendimientos();
      window.scrollTo(0, -1);
      this.$Progress.finish();
    }
  }
</script>
<style>
.zoom {
  transition: transform .2s; /* Animation */
}

.zoom:hover {
  transform: scale(1.01); /* (150% zoom)*/
  box-shadow: 0px 0px 10px .5px rgba(63,95,252,1);
}
</style>
