<template>
  <div class="content">
    <div class="container-fluid">
      <div class="row">
        <div class="col-md-12">
          
          <DatosRelacionadosEmprendimiento />
          <ContactoEmprendimiento />
          <RedesEmprendimiento />

          <div class="pt-4 pb-4">
            <div class="row">
              <div class="col-md-12 ">
                <b-button 
                  class="mt-3 btn-danger btn-fill" 
                  @click="cancelar()">Cancelar
                </b-button>

                <b-button 
                  class="mt-3 btn btn-info btn-fill" 
                  style="float: right;"
                  @click="nextPage()">
                  Siguiente
                </b-button>

              </div>
            </div>
          </div>

          <b-modal size="md" id="errors" hide-footer>
            <div class="row">
              <div class="col-md-12 pt-3">
                <ul>
                  <li v-for="item in errors">
                    {{ item.error }}
                  </li>
                </ul>
              </div>
            </div>
            <div class="pt-4">
              <b-button class="mt-3 pull-right btn-danger btn-fill" @click="resetModal">Aceptar</b-button>
            </div>
          </b-modal>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
  const URL_API = process.env.VUE_APP_URL_API;
  import axios from "axios";
  import DatosRelacionadosEmprendimiento from 'src/components/EditEmprendimiento/DatosRelacionadosEmprendimiento.vue'
  import ContactoEmprendimiento from 'src/components/EditEmprendimiento/ContactoEmprendimiento.vue'
  import RedesEmprendimiento from 'src/components/EditEmprendimiento/RedesEmprendimiento.vue'

  export default {
    components: {
      DatosRelacionadosEmprendimiento,
      ContactoEmprendimiento,
      RedesEmprendimiento
    },
    data () {
      return {
        id:null,
        emprendimiento: [],
        errors:[]
      }
    },   
    
    methods:{
      currentRoute(){
        this.id = this.$route.params.id;
        this.$store.commit('setTitulo', {setTitulo: 'Programa GIDE'})
        this.$store.commit('setSubTitulo', {setSubTitulo: 'editar 1/5'})
      },

      async getEmprendimientoEdit(){
        let emp = await axios.get(URL_API+'api/emprendimientosGIDE/'+this.id);

        this.$store.state.emprendimiento = emp.data.emprendimiento;
        //------------------Palabras claves------------------
        let pc=[];
        emp.data.palabras_claves.forEach(element => {
          pc.push(element.palabra_clave);
        });
        this.$store.state.emprendimiento.palabras_claves = pc;
        //---------------------------------------------------
        this.$store.state.emprendimiento.id_emprendedor = 1;
        this.$store.state.emprendimiento.emprendedores = emp.data.emprendedores;

        this.$store.state.emprendimiento.id_estadios = 1;
        this.$store.state.emprendimiento.estadios = emp.data.estadios;

        this.$store.state.emprendimiento.id_registros_mdn = 1;
        this.$store.state.emprendimiento.registros_mdn = emp.data.registros_mdn;

        this.$store.state.emprendimiento.id_registros_pdn = 1;
        this.$store.state.emprendimiento.registros_pdn = emp.data.registros_pdn;

        this.$store.state.emprendimiento.id_estado_perfil_equipo_emprendedor = 1;
        this.$store.state.emprendimiento.estado_perfil_equipo_emprendedor = emp.data.estado_perfil_equipo_emprendedor;
        
        this.$store.state.emprendimiento.id_perfiles_existentes_equipo_emprendedor = 1;
        this.$store.state.emprendimiento.perfiles_existentes_equipo_emprendedor = emp.data.perfiles_existentes_equipo_emprendedor;
        
        this.$store.state.emprendimiento.id_rol_miembro_equipo_emprendedor = 1;
        this.$store.state.emprendimiento.roles_miembros_equipo_emprendedor = emp.data.roles_miembros_equipo_emprendedor;
        

        this.$store.state.emprendimiento.id_financiamiento_obtenido_emprendimiento = 1;
        this.$store.state.emprendimiento.financiamiento_obtenido_emprendimiento = emp.data.financiamiento_obtenido_emprendimiento;
        
        this.$store.state.emprendimiento.id_premios_concursos_emprendimiento = 1;
        this.$store.state.emprendimiento.premios_concursos_emprendimiento = emp.data.premios_concursos_emprendimiento;
        
        this.$store.state.emprendimiento.id_capacitaciones_emprendimiento = 1;
        this.$store.state.emprendimiento.capacitaciones_emprendimiento = emp.data.capacitaciones_institucionales_emprendimiento;
        
        this.$store.state.emprendimiento.id_asesoria_emprendimiento = 1;
        this.$store.state.emprendimiento.asesorias_emprendimiento = emp.data.asesorias_emprendimiento;
        
        this.$store.state.emprendimiento.id_mentoria_emprendimiento = 1;
        this.$store.state.emprendimiento.mentorias_emprendimiento = emp.data.mentorias_emprendimiento;
        
        this.$store.state.emprendimiento.id_observacion_emprendimiento = 1;
        this.$store.state.emprendimiento.observaciones_emprendimiento = emp.data.observaciones_emprendimiento;
        
        this.$store.state.emprendimiento.id_estudios_mercados_emprendimiento = 1;
        this.$store.state.emprendimiento.estudios_mercados_emprendimiento = emp.data.estudios_mercados_emprendimiento;
        
        this.$store.state.emprendimiento.id_cooperacion_internacional_emprendimiento = 1;
        this.$store.state.emprendimiento.cooperaciones_internacionales_emprendimiento = emp.data.cooperaciones_internacionales_emprendimiento;
        
        this.$store.state.emprendimiento.id_red_internacional_emprendimiento = 1;
        this.$store.state.emprendimiento.redes_internacionales_emprendimiento = emp.data.redes_internacionales_emprendimiento;

        this.$store.state.emprendimiento.id_necesidad_capital_emprendimiento = 1;
        this.$store.state.emprendimiento.necesidad_capital_emprendimiento = emp.data.necesidad_capital_emprendimiento;
        
        this.$store.state.emprendimiento.id_necesidad_socio_emprendimiento = 1;
        this.$store.state.emprendimiento.necesidad_socio_emprendimiento = emp.data.necesidad_socio_emprendimiento;
        
        this.$store.state.emprendimiento.id_necesidad_asesoria_emprendimiento = 1;
        this.$store.state.emprendimiento.necesidad_asesoria_emprendimiento = emp.data.necesidad_asesoria_emprendimiento;
        
        this.$store.state.emprendimiento.id_necesidad_internacionalizacion_emprendimiento = 1;
        this.$store.state.emprendimiento.necesidad_internacionalizacion_emprendimiento = emp.data.necesidad_internacionalizacion_emprendimiento;
        
        this.$store.state.emprendimiento.id_necesidad_otra_emprendimiento = 1;
        this.$store.state.emprendimiento.necesidad_otra_emprendimiento = emp.data.necesidad_otra_emprendimiento;

      },

      verificarEmprendimiento(){
        if(this.$store.state.emprendimiento_edit==false){
          this.getEmprendimientoEdit();
          this.$store.state.emprendimiento_edit=true;
        }
      },

      nextPage(){
        this.errors=[];

        if(this.$store.state.emprendimiento.nombre==''){
          this.errors.push({'error':'Agregue el NOMBRE del emprendimiento antes de continuar'});
        }

        if(this.$store.state.emprendimiento.anio_inicio==null){
          this.errors.push({'error':'Seleccione el AO DE INICIO del emprendimiento antes de continuar'});
        }

        if(this.$store.state.emprendimiento.id_perfil==null){
          this.errors.push({'error':'Seleccione el PERFIL del emprendimiento antes de continuar'});
        }

        if(this.$store.state.emprendimiento.id_rubro_principal==null){
          this.errors.push({'error':'Seleccione el RUBRO PRIMARIO del emprendimiento antes de continuar'});
        }

        if(this.errors[0]){
          this.$root.$emit('bv::show::modal','errors');
        }else{
          this.$router.push('/admin/ProgramaGIDE/'+this.id+'/edit/2');
          this.$router.push( {name:'editGIDE2', params: { id: this.id }});
        }
      },
      resetModal(){
        this.errors=[];
        this.$root.$emit('bv::hide::modal','errors');
      },

      cancelar(){
        if (confirm('Seguro que desea cancelar la edicin?')) {
          this.$store.commit('clearEmprendimiento');
          this.$router.push( {name:'EmprendimientosGIDE'});
        }
      },
    },

    created(){
      this.$Progress.start(),
      window.scrollTo(0,-1);
      this.currentRoute();
      this.verificarEmprendimiento();
      this.$Progress.finish();
    }
  };
</script>
<style>

</style>
