<template>
	<div>
		<h4 class="pt-3">Cooperaciones Internacionales</h4>   
    <b-card>
      <b-table
          class="tab-cooperacion-internacional-emprendimiento"
          hover
          stacked="md"
          head-variant="dark"
          :items="this.cooperaciones_internacionales"
          :fields="fields"
          :sort-by.sync="sortBy"
          :sort-desc.sync="sortDesc"
          :sort-direction="sortDirection"
        >
          <template v-slot:cell(actions)="row">
            <b-button 
              size="sm" 
              @click="viewCooperacion(row.item, row.index, $event.target)" 
              class="btn btn-info btn-fill mr-1">
                <span class="fa fa-eye"></span>
            </b-button>
          </template>
        </b-table>
    </b-card>

    <b-modal id="viewModalCooperacion"  size="lg" title="Cooperacin" hide-footer>
      <b-col cols="12" class="pt-1">
        <b-row>
          <b-col class="titulo" md="3" xs="12">
            <b>Cooperacin:</b>
          </b-col>
          <b-col md="9" xs="12">
            {{ this.cooperacion_internacional.nombre }}
          </b-col>
        </b-row>

        <b-row>
          <b-col class="titulo" md="3" xs="12">
            <b>Institucin:</b>
          </b-col>
          <b-col md="9" xs="12">
            {{ this.cooperacion_internacional.institucion }}
          </b-col>
        </b-row>

        <b-row>
          <b-col class="titulo" md="3" xs="12">
            <b>Ao:</b>
          </b-col>
          <b-col md="9" xs="12">
            {{ this.cooperacion_internacional.anio }}
          </b-col>
        </b-row>

        <template v-if="this.cooperacion_internacional.descripcion !== ''">
          <b-row>
            <b-col class="titulo" md="3" xs="12">
              <b>Descripcin:</b>
            </b-col>
            <b-col md="9" xs="12">
              {{ this.cooperacion_internacional.descripcion }}
            </b-col>
          </b-row>
        </template>

        <b-row class="pt-4">
          <b-button class="mt-3 pull-left btn-danger btn-fill" @click="closeModal">Cerrar</b-button>
        </b-row>
      </b-col>
    </b-modal>
	</div>
</template>

<script>
  export default {
    props: ['cooperaciones_internacionales'],
    data() {
      return {
        cooperacion_internacional:{
          nombre:null,
          institucion:null,
          anio: null,
          descripcion: null
        },
        viewindex: null,
        fields: [
          { key: 'nombre',        label: 'Cooperacin',   sortable: true, thStyle: {width:'50%'} },
          { key: 'institucion',   label: 'Institucin',   sortable: true, thStyle: {width:'30%'} },
          { key: 'anio',          label: 'Ao',           sortable: true},
          { key: 'actions',       label: 'Vr' }
        ],
        sortBy: 'anio',
        sortDesc: true,
        sortDirection: 'desc',
      }
    },
    methods: {
      viewCooperacion(item, index, button) {
        this.cooperacion_internacional.nombre = item.nombre;
        this.cooperacion_internacional.institucion = item.institucion;
        this.cooperacion_internacional.anio = item.anio;
        this.cooperacion_internacional.descripcion = item.descripcion;

        this.$root.$emit('bv::show::modal', 'viewModalCooperacion', button);
      },

      closeModal(item, index, button){
        this.$root.$emit('bv::hide::modal', 'viewModalCooperacion', button)
      }
    }
  };
</script>
<style>
.tab-cooperacion-internacional-emprendimiento thead tr th{
  padding-top: .5rem;
  padding-bottom: .5rem;
}

.tab-cooperacion-internacional-emprendimiento thead tr th div{
  font-size: 14px;
  font-weight: 600;
  display: flex;
  justify-content: center;
}

@media screen and (min-width: 400px) {
  .tab-cooperacion-internacional-emprendimiento tbody tr td:nth-child(1) {
    text-align: left;
  }
  .tab-cooperacion-internacional-emprendimiento tbody tr td:nth-child(2) {
    text-align: left;
  }
}

@media screen and (min-width: 800px) {
  .tab-cooperacion-internacional-emprendimiento thead tr th:nth-child(3) {
    justify-content: center;
  }

  .tab-cooperacion-internacional-emprendimiento tbody tr td:nth-child(3) {
    text-align: center;
  }

  .tab-cooperacion-internacional-emprendimiento thead tr th:nth-child(4) {
    justify-content: center;
  }

  .tab-cooperacion-internacional-emprendimiento tbody tr td:nth-child(4) {
    justify-content: center;
  }

}
</style>