<template>
  <div class="content pt-4"> 
    <b-container fluid>

      <b-card>
        <b-row>
          <b-col cols="12" style="padding-top:1rem;">
            <label label-for="input-default">Apellido:</label>
            <b-form-input 
              type="text" 
              v-model="lastname" 
              required 
              autocomplete="off">
            </b-form-input>
          </b-col>

          <b-col cols="12" style="padding-top:1rem;">
            <label label-for="input-default">Nombre:</label>
            <b-form-input 
              type="text" 
              v-model="name" 
              required 
              autocomplete="off">
            </b-form-input>
          </b-col>

          <b-col cols="12" style="padding-top:1rem;">
            <label label-for="input-default">Correo electrnico:</label>
            <b-form-input 
              type="email" 
              v-model="email" 
              required 
              autocomplete="off">
            </b-form-input>
          </b-col>

          <b-col cols="12" style="padding-top:1rem;">
            <label label-for="input-default">Mis roles:</label>
            <br>
            <div v-for="r in roles" style="display: inline-block; padding:.2rem;">
              <label class="caja">{{ r.name }}</label>
            </div>
          </b-col>



          <b-col cols="12" style="padding-top:1rem;">
            <hr>
          </b-col>

          <b-col cols="12" style="padding-top:1rem;">
            <b-form-checkbox v-model="edit_password" name="check-button" switch>
              Cambiar contrasea
            </b-form-checkbox>
          </b-col>

          <b-col cols="12" style="padding-top:1rem;">
            <label label-for="input-default">Contrasea actual:</label>
            <b-input-group>
              <b-form-input 
                :state="validate_password"
                v-model="password" 
                :type="passwordFieldType1" 
                :disabled="!edit_password">
              </b-form-input>
              <b-input-group-prepend is-text>
                <b-form-checkbox 
                  switch class="mr-n2" 
                  @change="switchVisibility(1)" 
                  :disabled="!edit_password">
                </b-form-checkbox>
              </b-input-group-prepend>
            </b-input-group>
          </b-col>

          <b-col cols="12" style="padding-top:1rem;">
            <label label-for="input-default">Contrasea nueva:</label>
            <b-input-group>
              <b-form-input 
                :state="validate_new_password"
                v-model="new_password" 
                :type="passwordFieldType2" 
                :disabled="!edit_password">
              </b-form-input>
              <b-input-group-prepend is-text>
                <b-form-checkbox 
                  switch 
                  class="mr-n2" 
                  @change="switchVisibility(2)" 
                  :disabled="!edit_password">
                </b-form-checkbox>
              </b-input-group-prepend>
            </b-input-group>
          </b-col>

          <b-col cols="12" style="padding-top:1rem;">
            <label label-for="input-default">Confirmar nueva contrasea:</label>
            <b-input-group>
              <b-form-input
                :state="validate_new_password"
                v-model="new_password_confirm" 
                :type="passwordFieldType3" 
                :disabled="!edit_password">
              </b-form-input>
              <b-input-group-prepend is-text>
                <b-form-checkbox 
                  switch 
                  class="mr-n2" 
                  @change="switchVisibility(3)" 
                  :disabled="!edit_password">
                </b-form-checkbox>
              </b-input-group-prepend>
            </b-input-group>
          </b-col>


          <b-col cols="12" style="padding: 5rem 1rem 1rem 1rem;">
            <b-button
              class="mt-3 btn-danger btn-fill" 
              @click="cancelar">Cancelar
            </b-button>

            <b-button 
              class="mt-3 btn btn-info btn-fill" 
              style="float: right;"
              @click="validate">
              Actualizar
            </b-button>
          </b-col>
        </b-row>
      </b-card>
    </b-container>
  </div>
</template>
<script>
  import axios from "axios";
  const URL_API = process.env.VUE_APP_URL_API;

  export default {
    data () {
      return {
        id:null,
        name: '',
        lastname: '',
        email: '',
        email_confirm: '',
        edit_password: false,
        password:'',
        passwordFieldType1: 'password',
        validate_password:null,
        
        new_password:'',
        passwordFieldType2: 'password',
        validate_new_password:null,

        new_password_confirm:'',
        passwordFieldType3: 'password',

        roles:[],

      }
    },
    methods: {
      currentRoute(){
        this.$store.commit('setTitulo', {setTitulo: 'Perfil'});
        this.$store.commit('setSubTitulo', {setSubTitulo: 'editar'});
      },

      async getMe(){
        this.id = localStorage.getItem('id')
        try{
          let me = await axios.get(URL_API + "api/user-perfil/"+this.id);
          console.log(me);
        
          this.id = me.data.me.id;
          this.name = me.data.me.name;
          this.lastname = me.data.me.lastname;
          this.email = me.data.me.email;
          this.roles = me.data.roles;
        
        }catch (err) {
          console.log(me)
        }
        
      },

      switchVisibility(val) {
        switch (val) {
          case 1:
            this.passwordFieldType1 = this.passwordFieldType1 === 'password' ? 'text' : 'password'
            break;

          case 2:
            this.passwordFieldType2 = this.passwordFieldType2 === 'password' ? 'text' : 'password'
            break;
          
          case 3:
            this.passwordFieldType3 = this.passwordFieldType3 === 'password' ? 'text' : 'password'
            break;
        
          default:
            break;
        }
      },

      validate(){
        if(this.new_password == this.new_password_confirm){
          this.update();
        }else{
          this.validate_new_password=false;
        }
      },

      cancelar(){
        this.$router.push( {name:'EmprendimientosUNL'});
      },

      async update(){
        if(!this.edit_password){
          await axios.put(URL_API + "api/user-perfil/"+this.id, {
            name: this.name,
            lastname: this.lastname,
            email: this.email,
            edit_password: this.edit_password,
          }).then(response => {
            localStorage.setItem('name', this.name);
            localStorage.setItem('lastname', this.lastname);
            localStorage.setItem('email', this.email);
            this.$router.push( {name:'EmprendimientosUNL'});
          }).catch(e => {
              console.log(e);
          });
        }else{
          await axios.put(URL_API + "api/user-perfil/"+this.id, {
            name: this.name,
            lastname: this.lastname,
            email: this.email,
            edit_password: this.edit_password,
            password: this.password,
            new_password: this.new_password
          }).then(response => {
            localStorage.setItem('name', this.name);
            localStorage.setItem('lastname', this.lastname);
            localStorage.setItem('email', this.email);
            //this.$router.push( {name:'EmprendimientosUNL'});
            console.log("api Erorr: ", response)
          }).catch(err => console.log("api Erorr: ", err.response));
        }
        
      },

/*
      async update(){
        if(!this.edit_password){
          await axios.put(URL_API + "api/user-perfil/"+this.id, {
            name: this.name,
            lastname: this.lastname,
            email: this.email,
            edit_password: this.edit_password,
          }).then(response => {
            localStorage.setItem('name', this.name);
            localStorage.setItem('lastname', this.lastname);
            localStorage.setItem('email', this.email);
            this.$router.push( {name:'EmprendimientosUNL'});
          }).catch(e => {
              console.log(e);
          });
        }else{
          await axios.put(URL_API + "api/user-perfil/"+this.id, {
            name: this.name,
            lastname: this.lastname,
            email: this.email,
            edit_password: this.edit_password,
            password: this.password,
            new_password: this.new_password
          }).then(response => {
            localStorage.setItem('name', this.name);
            localStorage.setItem('lastname', this.lastname);
            localStorage.setItem('email', this.email);
            this.$router.push( {name:'EmprendimientosUNL'});
          }).catch(e => {
              console.log(e);
          });
        }
        
      },
*/

    },

    mounted() {
      this.getMe();
      this.currentRoute();
    },
  }

</script>
<style>
.caja {
  font-family: sans-serif;
  font-size: 18px;
  font-weight: 100;
  color: #ffffff!important;
  background: #889ccf;
  margin: 0 0 10px;
  overflow: hidden;
  padding: 10px;
  display: inline-block;
  border-radius: 5px;
}
</style>
