// configure router
const router = new VueRouter({
  routes, // short for routes: routes
  mode: 'history',
  linkActiveClass: 'nav-item active',
})

// GOOD
router.beforeEach((to, from, next) => {
  if (  to.name !== 'login' &&
        to.path !== '/' &&
        to.name !== 'SVyTT' &&
        !localStorage.getItem('isLogged')
      ){
    next({ name: 'login' })
  }
  next();
})

export default router