<template>
	<b-col>
		<h4 class="pt-3">Concursos  <small>Premio / mencin / reconocimiento</small></h4>   
    <b-card>
      <b-table
          class="table-premios-emprendimiento"
          hover
          stacked="md"
          head-variant="dark"
          :items="this.concursos_emprendimiento"
          :fields="fields"
          :sort-by.sync="sortBy"
          :sort-desc.sync="sortDesc"
          :sort-direction="sortDirection"
        >
          <template v-slot:cell(institucion)="row">
            {{ (row.item.institucion=='unl')?'Universidad Nacional del Litoral': row.item.otra_institucion }}
          </template>

          <template v-slot:cell(actions)="row">
            <b-button 
              size="sm" 
              @click="viewConcurso(row.item, row.index, $event.target)" 
              class="btn btn-info btn-fill mr-1">
                <span class="fa fa-eye"></span>
            </b-button>
          </template>
        </b-table>
    </b-card>

    <b-modal id="viewModalConcursos"  size="lg" title="Concursos" hide-footer>
      <b-col cols="12" class="pt-1">
        <b-row>
          <b-col class="titulo" md="3" xs="12">
            <b>Concurso:</b>
          </b-col>
          <b-col md="9" xs="12">
            {{ this.concurso_emprendimiento.nombre_concurso }}
          </b-col>
        </b-row>

        <b-row>
          <b-col class="titulo" md="3" xs="12">
            <b>Institucin:</b>
          </b-col>
          <b-col md="9" xs="12">
            {{ (this.concurso_emprendimiento.institucion =='unl')?'Universidad Nacional del Litoral':this.concurso_emprendimiento.otra_institucion }}
          </b-col>
        </b-row>

        <b-row>
          <b-col class="titulo" md="3" xs="12">
            <b>Ao:</b>
          </b-col>
          <b-col md="9" xs="12">
            {{ this.concurso_emprendimiento.anio }}
          </b-col>
        </b-row>

        <template v-if="this.concurso_emprendimiento.observacion !== ''">
          <b-row>
            <b-col class="titulo" md="3" xs="12">
              <b>Observaciones:</b>
            </b-col>
            <b-col md="9" xs="12">
              {{ this.concurso_emprendimiento.observacion }}
            </b-col>
          </b-row>
        </template>

        <b-row class="pt-4">
          <b-button class="mt-3 pull-left btn-danger btn-fill" @click="closeModal">Cerrar</b-button>
        </b-row>
      </b-col>
    </b-modal>
	</b-col>
</template>

<script>
  export default {
    props: ['concursos_emprendimiento'],
    data() {
      return {
        concurso_emprendimiento:{
          nombre_concurso:null,
          institucion:null,
          otra_institucion:null,
          anio: null,
          observacion: null
        },
        viewindex: null,
        fields: [
          { key: 'nombre_concurso', label: 'Concurso',    sortable: true, thStyle: {width:'50%'} },
          { key: 'institucion',     label: 'Institucin', sortable: true, thStyle: {width:'30%'} },
          { key: 'anio',            label: 'Ao',         sortable: true},
          { key: 'actions',         label: 'Vr' }
        ],
        sortBy: 'anio',
        sortDesc: true,
        sortDirection: 'desc',
      }
    },
    methods: {
      viewConcurso(item, index, button) {

        
        this.concurso_emprendimiento.nombre_concurso = item.nombre_concurso;
        this.concurso_emprendimiento.institucion = item.institucion;
        this.concurso_emprendimiento.otra_institucion = item.otra_institucion;
        this.concurso_emprendimiento.anio = item.anio;
        this.concurso_emprendimiento.observacion = item.observacion;

        this.$root.$emit('bv::show::modal', 'viewModalConcursos', button);
      },

      closeModal(item, index, button){
        this.$root.$emit('bv::hide::modal', 'viewModalConcursos', button)
      }
    }
  };
</script>
<style>
.table-premios-emprendimiento thead tr th{
  padding-top: .5rem;
  padding-bottom: .5rem;
}

.table-premios-emprendimiento thead tr th div{
  font-size: 14px;
  font-weight: 600;
  display: flex;
  justify-content: center;
}

@media screen and (min-width: 400px) {
  .table-premios-emprendimiento tbody tr td:nth-child(1) {
    text-align: left;
  }
  .table-premios-emprendimiento tbody tr td:nth-child(2) {
    text-align: left;
  }
}

@media screen and (min-width: 800px) {

  .table-premios-emprendimiento thead tr th:nth-child(3) {
    justify-content: center;
  }

  .table-premios-emprendimiento tbody tr td:nth-child(3) {
    text-align: center;
  }

  .table-premios-emprendimiento thead tr th:nth-child(4) {
    justify-content: center;
  }

  .table-premios-emprendimiento tbody tr td:nth-child(4) {
    justify-content: center;
  }

}
</style>