<template>
  <div class="content"> 
    <b-container fluid class="pt-4">
      <b-card style="min-height: 40rem;">
        <div class="col-md-12">
          <!-- User Interface controls -->
          <b-row>
            <b-col lg="12" class="my-4">
              <b-form-group
                label-cols-sm="8"
                label-align-sm="right"
                label-size="md"
                label-for="filterInput"  
              >
                <b-form-input
                  v-model="filter"
                  type="search"
                  id="filterInput"
                  autocomplete="off"
                  size="md"
                  placeholder="Buscr..."
                ></b-form-input>
              </b-form-group>
            </b-col>
          </b-row>
        </div>
        <!-- Main table element -->
        <div class="col-md-12 pt-1">
          <b-table
            class="table-emprendimientos"
            hover
            stacked="md"
            head-variant="dark"
            :items="emprendimientos"
            :fields="fields"
            :current-page="currentPageEmprendimientosUNL"
            :per-page="perPageEmprendimientosUNL"
            :filter="filter"
            :filterIncludedFields="filterOn"
            :sort-by.sync="sortBy"
            :sort-desc.sync="sortDesc"
            :sort-direction="sortDirection"
            @filtered="onFiltered"
          >
            <template v-slot:cell(actions)="row">
              <b-button size="sm" @click="show(row.item, row.index, $event.target)" class="btn btn-info btn-fill mr-1">
                <span class="fa fa-eye"></span>
              </b-button>
            </template>
          </b-table>
        </div>
        <b-row>
          <b-col sm="12" md="5">
            <b-col>
              <b-form-group
                label="Por pgina"
                label-cols-sm="4"
                label-cols-md="7"
                label-cols-lg="8"
                label-align-sm="right"
                label-size="sm"
                label-for="perPageSelect"
                class="mb-0"
              >
                <b-form-select
                  v-model="perPageEmprendimientosUNL"
                  id="perPageSelect"
                  size="sm"
                  :options="pageOptions"
                ></b-form-select>
              </b-form-group>
            </b-col>
          </b-col>
          
          <b-col sm="12" md="5" class="my-1">
            <b-pagination
              v-model="currentPageEmprendimientosUNL"
              :total-rows="totalRows"
              :per-page="perPageEmprendimientosUNL"
              align="center"
              size="sm"
              class="my-0"
            ></b-pagination>
          </b-col>
        </b-row>
      </b-card>
    </b-container>
  </div>
</template>

<script>
  import auth from "@/logic/auth";
  import axios from "axios";
  const URL_API = process.env.VUE_APP_URL_API;
  
  export default {
    data() {
      return {
        isLoading:true,
        ruta: null,
        emprendimientos: [],
        editindex: null,
        deleteIndex: null,
        fields: [
          { key: 'id', label: '#', sortDirection: 'desc', sortable: true, thStyle: {width:'8%'}},
          { key: 'nombre', label: 'Emprendimiento', sortable: true, thStyle: {width:'42%'}},
          { key: 'created_by', label: 'Programa', sortable: true,thStyle: {width:'20%'} },
          { key: 'perfil', label: 'Perfl', sortable: true,thStyle: {width:'20%'} },
          { key: 'actions', label: 'Acciones'}
        ],
        totalRows: 99999,
        pageOptions: [10, 25, 50, 100, 200],
        sortBy: 'id',
        sortDesc: true,
        sortDirection: 'asc',
        filter: null,
        filterOn: [],
      }
    },

    computed: {
      sortOptions() {
        // Create an options list from our fields
        return this.fields
          .filter(f => f.sortable)
          .map(f => {
            return { text: f.label, value: f.key }
          })
      },

      perPageEmprendimientosUNL:{
        get(){
          return this.$store.state.perPage
        },
        set(value){
          this.$store.commit('setPerPage', {perPage: value})
        }
      },

      currentPageEmprendimientosUNL:{
        get(){
          return this.$store.state.currentPage
        },
        set(value){
          this.$store.commit('setCurrentPage', {currentPage: value})
        }
      }
    },

    mounted() {
      // Set the initial number of items
      window.scrollTo(0, -1);
    },

    methods: {  
      onFiltered(filteredItems) {
        // Trigger pagination to update the number of buttons/pages due to filtering
        this.totalRows = filteredItems.length;
        this.currentPage = 1;
      },

      currentRoute(){
          this.$store.commit('setTitulo', {setTitulo: 'Emprendedores UNL'})
          this.$store.commit('setSubTitulo', {setSubTitulo: ''})
      },

      async getEmprendimientos(){
        await axios.get(URL_API+'api/emprendimientos').then(response =>{
            response.data.forEach(function(element, index, rubros){
            if(element.created_by=='programa_bio'){
              element.created_by= 'UNLBio';
            }
            if(element.created_by=='programa_emp'){
              element.created_by= 'Emprendedores';
            }
            if(element.created_by=='programa_gide'){
              element.created_by= 'GIDE';
            }
          });
          this.emprendimientos = response.data;
          this.totalRows = this.emprendimientos.length;
          this.isLoading = false;
        }).catch(error => {
          alert("Oops ha ocurrido un error durante la carga, intente mas tarde.");
          console.log(error);
        })
      },

      show(item, index, button){
        this.$router.push({ path: 'EmprendimientosUNL/ver/'+item.id})
      },
    },

    created(){
      this.$Progress.start(),
      this.getEmprendimientos(),
      this.currentRoute(),
      this.$Progress.finish()
    }
  }
</script>

<style>

.btn-close {
  background-color: red;
}

.table-emprendimientos {
  table-layout: fixed;
}

.table-emprendimientos thead th div{
  font-size: 14px;
  font-weight: 600;
  display: flex;
  justify-content: center;

}


.table-emprendimientos thead tr th{
  padding-top: .5rem;
  padding-bottom: .5rem;
}


@media screen and (min-width: 400px) {
  .table-emprendimientos tbody tr td:nth-child(1) {
    text-align: left;
  }
  .table-emprendimientos tbody tr td:nth-child(3) {
    text-transform: capitalize;
    text-align: left;
  }
}

@media screen and (min-width: 800px) {
  .table-emprendimientos tbody tr td:nth-child(1) {
    text-align: center;
  }

  .table-emprendimientos tbody tr td:nth-child(3) {
    text-align: center;
  }

  .table-emprendimientos tbody tr td:nth-child(4) {
    text-transform: capitalize;
    text-align: center;
  }

  .table-emprendimientos thead tr th:nth-child(5) {
    justify-content: center!important;
  }

  .table-emprendimientos tbody tr td:nth-child(5) {
    justify-content: center;
  }
}

input[type=number]::-webkit-inner-spin-button, 
  input[type=number]::-webkit-outer-spin-button { 
    -webkit-appearance: none; 
    margin: 0; 
  }
</style>