<template>
	<div>
		<h4 class="pt-3">Otras necesidades</h4>   
    <b-card>
      <b-table
          class="table-otras-necesidades-emprendimiento"
          hover
          stacked="md"
          head-variant="dark"
          :items="this.necesidad_otras"
          :fields="fields"
          :sort-by.sync="sortBy"
          :sort-desc.sync="sortDesc"
          :sort-direction="sortDirection"
        >
          <template v-slot:cell(actions)="row">
            <b-button 
              size="sm" 
              @click="viewOtraNecesidad(row.item, row.index, $event.target)" 
              class="btn btn-info btn-fill mr-1">
                <span class="fa fa-eye"></span>
            </b-button>
          </template>
        </b-table>
    </b-card>

    <b-modal id="viewModalOtraNecesidad"  size="lg" title="Otra necesidad" hide-footer>
      <b-col cols="12" class="pt-1">
        <b-row>
          <b-col class="titulo" md="3" xs="12">
            <b>Necesidad:</b>
          </b-col>
          <b-col md="9" xs="12">
            {{ this.necesidad_otra.necesidad }}
          </b-col>
        </b-row>

        <b-row>
          <b-col class="titulo" md="3" xs="12">
            <b>Ao:</b>
          </b-col>
          <b-col md="9" xs="12">
            {{ this.necesidad_otra.anio }}
          </b-col>
        </b-row>
        
        <template v-if="this.necesidad_otra.descripcion !== ''">
          <b-row>
          <b-col class="titulo" md="3" xs="12">
            <b>Descripcin:</b>
          </b-col>
          <b-col md="9" xs="12">
            {{ this.necesidad_otra.descripcion }}
          </b-col>
        </b-row>
        </template>

        <b-row class="pt-4">
          <b-button class="mt-3 pull-left btn-danger btn-fill" @click="closeModal">Cerrar</b-button>
        </b-row>
      </b-col>
    </b-modal>
	</div>
</template>

<script>
  export default {
    props: ['necesidad_otras'],
    data() {
      return {
        necesidad_otra:{
          necesidad:null,
          anio: null,
          descripcion: null
        },
        viewindex: null,
        fields: [
          { key: 'necesidad',     label: 'Necesidad',   sortable: true, thStyle: {width:'80%'} },
          { key: 'anio',          label: 'Ao',         sortable: true},
          { key: 'actions',       label: 'Vr' }
        ],
        sortBy: 'anio',
        sortDesc: true,
        sortDirection: 'desc',
      }
    },
    methods: {
      viewOtraNecesidad(item, index, button) {
        this.necesidad_otra.necesidad = item.necesidad;
        this.necesidad_otra.anio = item.anio;
        this.necesidad_otra.descripcion = item.descripcion;

        this.$root.$emit('bv::show::modal', 'viewModalOtraNecesidad', button);
      },

      closeModal(item, index, button){
        this.$root.$emit('bv::hide::modal', 'viewModalOtraNecesidad', button)
      }
    }
  };
</script>
<style>
.table-otras-necesidades-emprendimiento thead tr th{
  padding-top: .5rem;
  padding-bottom: .5rem;
}

.table-otras-necesidades-emprendimiento thead tr th div{
  font-size: 14px;
  font-weight: 600;
  display: flex;
  justify-content: center;
}

@media screen and (min-width: 400px) {
  .table-otras-necesidades-emprendimiento tbody tr td:nth-child(1) {
    text-align: left;
  }
  .table-otras-necesidades-emprendimiento tbody tr td:nth-child(2) {
    text-align: left;
  }
}

@media screen and (min-width: 800px) {

  .table-otras-necesidades-emprendimiento thead tr th:nth-child(2) {
    justify-content: center;
  }

  .table-otras-necesidades-emprendimiento tbody tr td:nth-child(2) {
    text-align: center;
  }

  .table-otras-necesidades-emprendimiento thead tr th:nth-child(3) {
    justify-content: center;
  }

  .table-otras-necesidades-emprendimiento tbody tr td:nth-child(3) {
    justify-content: center;
  }

}
</style>