<template>
  <div class="content">
    <div class="container-fluid">
      <div class="row">
        <div class="col-md-12">

          <EmprendedoresEmprendimiento />
         
          <div class="pt-4 pb-4">
            <div class="row">
              <div class="col-md-12 ">

                <router-link class="mt-3 btn btn-danger btn-fill"
                  :to="({name: 'createBIO1'})" >Volver
                </router-link>
                
                <b-button 
                  class="mt-3 btn btn-info btn-fill" 
                  style="float: right;"
                  @click="siguiente">
                  Siguiente
                </b-button>
              
              </div>
            </div>
          </div>

          <b-modal size="md" id="errors" hide-footer>
            <div class="row">
              <div class="col-md-12 pt-3">
                <ul>
                  <li v-for="item in errors">
                    {{ item.error }}
                  </li>
                </ul>
              </div>
            </div>
            <div class="pt-4">
              <b-button 
                class="mt-3 pull-right btn-danger btn-fill" 
                @click="resetModalErrors">
                Aceptar
              </b-button>
            </div>
          </b-modal>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from "axios";
  const URL_API = process.env.VUE_APP_URL_API;
  import EmprendedoresEmprendimiento from 'src/components/CreateEmprendimiento/EmprendedoresEmprendimiento.vue'

  export default {
    components: {
      EmprendedoresEmprendimiento
    },
    data () {
      return {
        errors:[]
      }
    },
    methods:{
      currentRoute(){
          this.$store.commit('setSubTitulo', {setSubTitulo: 'nuevo 2/5'})
      },

      verificarEmprendimiento(){
        if(this.$store.state.emprendimiento_create==false){
          this.$router.push( {name:'createBIO1'});
        }
      },

      siguiente(){
        this.errors=[];

        if(this.$store.state.emprendimiento.emprendedores.length==0){
          this.errors.push({'error':'Agregue al menos un INTEGRANTE del emprendimiento antes de continuar'});
        }

        if(this.$store.state.emprendimiento.dni_responsable_emprendimiento==null){
          this.errors.push({'error':'Seleccione el RESPONSABLE del emprendimiento antes de continuar'});
        }

        if(this.errors[0]){
          this.$root.$emit('bv::show::modal','errors');
        }else{
          this.$router.push( {name:'createBIO3'});
        }
      },
      resetModalErrors(){
        this.errors=[];
        this.$root.$emit('bv::hide::modal','errors');
      },
    },
    created(){
      this.$Progress.start();
      window.scrollTo(0,-1);
      this.verificarEmprendimiento();
      this.currentRoute();
      this.$Progress.finish();
    },
  };
</script>
<style>
.table-emprendedores {
  table-layout: fixed;
}

.table-emprendedores thead th div{
  font-size: 14px;
  font-weight: 600;
  display: flex;
  justify-content: center;
}


.table-emprendedores thead tr th{
  padding-top: .5rem;
  padding-bottom: .5rem;
}

@media screen and (min-width: 400px) {
  .table-emprendedores tbody tr td:nth-child(1) {
    text-align: left;
  }
  .table-emprendedores tbody tr td:nth-child(3) {
    text-align: left;
  }
}

@media screen and (min-width: 800px) {
  .table-emprendedores tbody tr td:nth-child(1) {
    text-align: center;
  }

  .table-emprendedores tbody tr td:nth-child(1) {
    text-align: center;
  }

  .table-emprendedores thead tr th:nth-child(4) {
    justify-content: center;
  }

  .table-emprendedores tbody tr td:nth-child(4) {
    justify-content: center;
  }
}
</style>
