<template>
  <div class="content pt-4"> 
    <b-container fluid>
      <div class="card">
        <div class="col-md-12">
          <!-- User Interface controls -->
          <b-row>
            <b-col lg="6" class="my-4">
              <b-button 
                @click="nuevo" 
                class="btn btn-info btn-fill">
                  <span class="fa fa-plus"></span> Nuevo
              </b-button>
            </b-col>
            <b-col lg="6" class="my-4">
              <b-form-group
                label-cols-sm="7"
                label-align-sm="right"
                label-size="md"
                label-for="filterInput"  
              >
              <b-form-input
                v-model="filter"
                type="search"
                id="filterInput"
                autocomplete="off"
                size="md"
                placeholder="Buscr"
              ></b-form-input>
              </b-form-group>
            </b-col>
          </b-row>
        </div>
        <!-- Main table element -->
        <div class="col-md-12 pt-4">
          <b-table
            class="table-incubadoras-gide"
            hover
            head-variant="dark"
            stacked="md"
            :items="incubadorasGIDE"
            :fields="fields"
            :current-page="currentPage"
            :per-page="perPage"
            :filter="filter"
            :filterIncludedFields="filterOn"
            :sort-by.sync="sortBy"
            :sort-desc.sync="sortDesc"
            :sort-direction="sortDirection"
            @filtered="onFiltered"
          >
            <template v-slot:cell(actions)="row">
              <b-button size="sm" @click="edit(row.item, row.index, $event.target)" class="btn btn-warning btn-fill mr-1">
                <span class="fa fa-pencil"></span>
              </b-button>
              <b-button class="btn-danger btn-fill" size="sm" @click="deleteincubadora(row.item, row.index, $event.target)">
                <span class="fa fa-trash"></span>
              </b-button>
            </template>

            <template v-slot:row-details="row">
              <b-card>
                <ul>
                  <li v-for="(value, key) in row.item" :key="key">{{ key }}: {{ value }}</li>
                </ul>
              </b-card>
            </template>
          </b-table>
        </div>
        <b-row>
          <b-col sm="5" md="3" class="my-1">
            <b-form-group
              label="Por pagina"
              label-cols-sm="8"
              label-cols-md="8"
              label-cols-lg="8"
              label-align-sm="right"
              label-size="sm"
              label-for="perPageSelect"
              class="mb-0"
            >
              <b-form-select
                v-model="perPage"
                id="perPageSelect"
                size="sm"
                :options="pageOptions"
              ></b-form-select>
            </b-form-group>
          </b-col>
          <b-col sm="7" md="3" class="my-1">
            <b-pagination
              v-model="currentPage"
              :total-rows="totalRows"
              :per-page="perPage"
              align="fill"
              size="sm"
              class="my-0"
            ></b-pagination>
          </b-col>
        </b-row>
      </div>
      <b-modal size="md" id="create-modal" hide-footer>
        <div class="row">
          <div class="col-md-12">
            <p>Nuevo incubadora:</p>
            <b-form-input v-model="incubadora" @keydown.enter="addNuevo" required autocomplete="off"></b-form-input>
          </div>
        </div>
        <div class="pt-4">
          <b-button class="mt-3 pull-left btn-danger btn-fill" @click="resetModal">Cacelar</b-button>
          <b-button class="mt-3 pull-right btn-info btn-fill" @click="addNuevo">Agregar</b-button>
        </div>
      </b-modal>

      <!-- Edit modal -->
      <b-modal :id="editModal.id"  size="md" hide-footer>

          <div class="row">
            <div class="col-md-12">
              <p>Editar incubadora:</p>
              <b-form-input v-model="incubadora" @keydown.enter="update" required autocomplete="off"></b-form-input>
            </div>
          </div>
          <div class="pt-4">
            <b-button class="mt-3 pull-left btn-danger btn-fill" @click="resetModal">Cacelar</b-button>
            <b-button class="mt-3 pull-right btn-info btn-fill" @click="update">Actualizar</b-button>
          </div>

      </b-modal>

    </b-container>
  </div>
</template>

<script>
  import axios from "axios";
  const URL_API = process.env.VUE_APP_URL_API;

  export default {
    data() {
      return {
        incubadorasGIDE: [],
        incubadora: '',
        editindex: null,
        deleteIndex: null,
        fields: [
          { key: 'id', label: '#', sortable: true, thStyle: {width:'7%'}},
          { key: 'nombre', label: 'Incubadoras GIDE', sortable: true, sortDirection: 'desc', thStyle: {width:'75%'}},
          { key: 'actions', label: 'Acciones'}
        ],
        totalRows: 1,
        currentPage: 1,
        perPage: 25,
        pageOptions: [10, 25, 50],
        sortBy: '',
        sortDesc: false,
        sortDirection: 'asc',
        filter: null,
        filterOn: [],
        editModal: {
          id: 'info-modal',
          title: '',
          content: ''
        }
      }
    },

    computed: {
      sortOptions() {
        // Create an options list from our fields
        return this.fields
          .filter(f => f.sortable)
          .map(f => {
            return { text: f.label, value: f.key }
          })
      }
    },

    mounted() {
      // Set the initial number of items
      this.totalRows = this.incubadorasGIDE.length;
      this.$store.commit('setTitulo', {setTitulo: 'Configuracin'});
      this.$store.commit('setSubTitulo', {setSubTitulo: 'Incubadoras GIDE'});
    },

    methods: {

      onFiltered(filteredItems) {
        // Trigger pagination to update the number of buttons/pages due to filtering
        this.totalRows = filteredItems.length
        this.currentPage = 1
      },

      async getincubadorasGIDE(){
        let r = await axios.get(URL_API+'api/incubadoras-gide');
        this.incubadorasGIDE = r.data;
      },

      nuevo() {
        this.incubadora = '';
        this.$root.$emit('bv::show::modal', 'create-modal');
      },

      async addNuevo(){
        axios.post(URL_API+'api/incubadoras-gide', {
            nombre: this.incubadora,
        }).then(response => {
          this.incubadora='';
          this.getincubadorasGIDE();
        }).catch(e => {
            console.log(e);
        });
        this.$root.$emit('bv::hide::modal','create-modal')
      },

      resetModal(item, index, button) {
        this.incubadora = '';
        this.$root.$emit('bv::hide::modal','create-modal');
        this.$root.$emit('bv::hide::modal', this.editModal.id, button)
      },

      edit(item, index, button) {
        this.editindex = item.id;
        this.editModal.title =  '';
        this.incubadora = item.nombre;
        this.$root.$emit('bv::show::modal', this.editModal.id, button);
      },

      async update(item, index, button){
        await axios.put(URL_API+'api/incubadoras-gide/'+this.editindex, {
            nombre: this.incubadora,
        }).then(response => {
          this.incubadora='';
          this.getincubadorasGIDE();
          this.resetModal();
        }).catch(e => {
            console.log(e);
            this.resetModal();
        });
      },

      async deleteincubadora(item, index, button){
        this.deleteIndex = item.id;
        if (confirm('Seguro que desea eliminarlo?')) {
          axios.delete(URL_API+'api/incubadoras-gide/'+this.deleteIndex)
          .then(response => {
            this.deleteIndex = null;
            this.getincubadorasGIDE();
          }).catch(e => {
              console.log(e);
          });
        }
      }
    },

    created(){
      this.$Progress.start(),
      this.getincubadorasGIDE(),
      this.$Progress.finish()
    }
  }
</script>

<style>
.btn-close {
  background-color: red;
}

.table-incubadoras-gide {
  table-layout: fixed;
}

.table-incubadoras-gide thead th div{
  font-size: 14px;
  font-weight: 600;
  display: flex;
  justify-content: center;

}

.table-incubadoras-gide thead tr th{
  padding-top: .5rem;
  padding-bottom: .5rem;
}


@media screen and (min-width: 400px) {
  .table-incubadoras-gide tbody tr td:nth-child(1) {
    text-align: left;
  }
  .table-incubadoras-gide tbody tr td:nth-child(3) {
    text-align: left;
  }
}

@media screen and (min-width: 800px) {
  .table-incubadoras-gide tbody tr td:nth-child(1) {
    text-align: center;
  }

  .table-incubadoras-gide thead tr th:nth-child(3) {
    justify-content: center;
  }

  .table-incubadoras-gide tbody tr td:nth-child(3) {
    justify-content: center;
  }
}
</style>